<template>
  <section class="cat">
    <div class="container">
      <h2 class="cat__h">Биография</h2>
      <div class="main__wrap">
        <div
          class="product"
          v-for="product in productsByCategory.biography"
          :key="product.id"
          @click="showProductDetails(product)"
        >
          <div class="product__wrap">
            <img :src="convertImageUrl(product.image.url)" alt="" class="product__img" />
            <div class="product__desc">
              <h1 class="product__h">{{ product.name }}</h1>
              <h1 class="product__price">{{ product.price.formatted }}0 Сум</h1>
            </div>
          </div>
        </div>
      </div>
      <div class="loading-overlay"  v-if="loading">
        <div class="loading-circle"></div>
      </div>
    </div>
  </section>
  <Product
    v-if="selectedProduct"
    :product="selectedProduct"
    @back="clearSelectedProduct"
  />
</template>

<script>
import Product from "@/components/Product/Product.vue";
import { mapState, mapActions } from "vuex";

export default {
  components: {
    Product,
  },
  computed: {
    ...mapState(["productsByCategory", "selectedProduct", "loading"]),
  },
  methods: {
    ...mapActions([
      "fetchProductsByCategory",
      "clearSelectedProduct",
      "selectProduct",
    ]),
    showProductDetails(product) {
      this.selectProduct(product);
    },
    convertImageUrl(url) {
      return url.replace(/\|/g, '%7C');
    },
  },
  created() {
    this.fetchProductsByCategory({ categoryId: "biography" });
  },
};
</script>

